//Обнуляющий список
@import 'null_styles'; 

$contSize: 1050px;
$mainClr: rgb(81, 118, 187);
$listBg: rgb(158, 123, 179);
$listBg-hover: rgb(171, 140, 189);
$mainFF: Glory;
$itemFF: ('Noto Sans JP', sans-serif);

input::-webkit-input-placeholder { color: rgba(255, 255, 255, 0.300); }

body {
   // background-color: rgba(126, 192, 190, 0.651);
   background-color: rgb(158, 123, 179);
}

.container {
   // margin: 0 auto;
   // width: 100%;
   // max-width: $contSize;
   // padding: 0px 20px 0px 20px;
}

.header-wrapper {
   padding: 0px 20px 0px 20px;
   position: fixed;
   top: 0;
   left: 0;
   height: 60px;
   width: 100%;
   background-color: $mainClr;
}
.header {
   height: 100%;
   width: 100%;
   font-family: $mainFF;
   color: #fff;
   font-size: 26px;
   display: flex;
   align-items: center;
   justify-content: space-between;

   &__title {
      flex: 0 0 auto;

      &-pc {
         
      }
      &-mobile {
         display: none;
      }

      @media(max-width: 520px) {
         &-pc {
            display: none;
         }
         &-mobile {
            display: block;
         }
      }
   }

   &__logo {
      width: 175px;
      flex: 0 0 auto;
      margin: 0px 0px 0px 25px;
      
      > img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }

      @media(max-width: 730px) {
         display: none;

         > img {
         }
      }
   }

   &__search {
      margin: 0px 0px 0px 25px;
      width: 100%;
      max-width: 380px;
      min-width: 200px;
      > input {
         color: #fff;
         height: 40px;
         width: 100%;
         background-color: rgba(255, 255, 255, 0.150);
         // border-radius: 5px;
      }
   }

   &__menu {
      display: none;
   }
}


.cards-wrapper {
   margin: 60px 0px 0px 0px;
}
.cards {
   
   
   &-list {
      padding: 0px 20px 0px 20px;
      background-color: $listBg;
      padding: 50px 50px 0px 50px;
      display: grid;
      gap: 30px;
      // grid-template-columns: 1fr 1fr 1fr 1fr;
      // grid-auto-columns: min-content;
       grid-template-columns: repeat(auto-fill, 415px);
      font-family: $mainFF;
      justify-content: center;

      @media(max-width:450px) {
         padding: 10px;
         display: block;
      }

      &__item {
         border: 2px solid rgb(119, 155, 158);
         background-color: #fff;
         border-radius: 10px;
         padding: 25px;
         display: flex;
         flex-direction: column;
         align-items: center;
         text-align: center;
         font-family: $itemFF;
         font-weight: 300;

         @media(max-width:550px) {
            padding: 10px;
         }
      }

      &__logo {

         > img {
            width: 100%;
            height: 100%;
            object-fit: cover;
         }
      }

      &__title {
         margin: 20px 0px 0px 0px;
      }

      &__description {
         margin: 30px 0px 0px 0px;
         flex: 1 1 auto;
      }

      &__button-area {
         margin: 20px 0px 0px 0px;
      }

      &__button {
         font-size: 18px;
         background-color: $listBg;
         color: #fff;
         padding: 7px 14px;

         &:hover {
            background-color: $listBg-hover;
         }
      }
   }  
}
